    onSelectionChange() {
        setTimeout(() => {
            if (this.currentTab === 0) {
                this.firstPageOptionHandle()
            }
            if (this.currentTab === 1) {
                this.secondPageOptionHandle()
            }
            if (this.currentTab === 2) {
                this.thirdPageSecondOptionHandle()
            }

            if (this.currentTab === 4){
                this.fifthPageOptionHandle()
            }

            if (this.currentTab === 6) {
                this.seventhPageOptionHandle()
            }
            if (this.currentTab === 7) {
                this.eighthPageOptionHandle()
            }
            if (this.data.length <= 7) {
                if (this.currentTab === 5) {
                    this.seventhPageOptionHandle()
                }
            } else {
                if (this.currentTab === 5) {
                    this.sixthPageOptionHandle()
                }
            }
        }, 50)


    }

